import * as React from "react";
import { Breadcrumbs, Button, ButtonGroup, FormControl, FormHelperText, Hidden, IconButton, List, ListItem, ListItemSecondaryAction, ListItemText, Modal, Paper, Select, TextField, Typography } from "@material-ui/core";
import Navbar from "../../../components/ui/Navbar";
import { Link } from "gatsby";
import ajax, { useFetcher } from "../../../components/ajax";
import { navigate } from "@reach/router";
import { getSearchParam } from "../../../components/util";
import { categories, keyedCategories } from ".";

const Page = () => {
    const id = getSearchParam().get('id') || null;
    const item = useFetcher(id && '/desa/ask/' + id);
    const [message, setMessage] = React.useState('');
    const [category, setCategory] = React.useState(categories[0].type);
    React.useEffect(() => {
        if (item) {
            setMessage(item.data.message);
            setCategory(item.data.category);
        }
    }, item);
    const save = function () {
        ajax.post('/desa/ask/' + (id || ''), {
            message,
            category
        }).then((data) => {
            alert('Berhasil disimpan');
            navigate('/desa/komplain/detail?id=' + (id || data.data.id));
        }).catch(x => {
            alert(x.response?.data.message || x.message);
        });
    }
    return (
        <div>
            <Navbar />
            <List>
                <ListItem>
                    <Breadcrumbs>
                        <Link color="inherit" to="/user/">
                            Layanan Desa
                        </Link>
                        <Link color="inherit" to="/desa/komplain/">
                            Layanan Komplain
                        </Link>
                        <Typography color="textPrimary">{id ? 'Edit' : 'Baru'}</Typography>
                    </Breadcrumbs>
                </ListItem>
                <ListItem>
                    {keyedCategories[category] && React.createElement(keyedCategories[category].icon, { style: { fontSize: '2em', marginRight: 15 } })}
                    <ListItemText primary="Kategori" />
                    <ListItemSecondaryAction>
                        <FormControl style={{ minWidth: '50vw' }} >
                            <Select native value={category} onChange={(x) => { setCategory(x.target.value); }}>
                                {categories.map(x => <option key={x.type} value={x.type}>{x.title}</option>)}
                            </Select>
                        </FormControl>
                    </ListItemSecondaryAction>

                </ListItem>
                <ListItem>
                    <FormControl fullWidth style={{ margin: '10px 0' }}>
                        <TextField multiline fullWidth value={message} onChange={(x) => setMessage(x.target.value)} label="Pesan" variant="outlined" />
                    </FormControl>
                </ListItem>
                <ListItem>
                    <ListItemText primary={id ? "Anda masih dapat mengedit ini selama belum direspon oleh perangkat desa" : "Keluhan atau saran anda akan terlihat untuk publik tanpa memberitahukan identitas anda (kecuali untuk perangkat desa sendiri)"} primaryTypographyProps={{ variant: 'body2' }} />
                </ListItem>
                <ListItem>
                    <Button disabled={message.length < 20 || message.length > 2000} onClick={save} variant="contained" color="primary">
                        Simpan
                    </Button>
                </ListItem>
            </List>
        </div>
    );
}

export default Page
